body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-header {
  display: -webkit-flex;
  display: flex;
  height: 100px;
  background: #282c34;
  color: #fff;
  -webkit-align-items: center;
          align-items: center; }
  .App-header .App-logo-container {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    margin-left: -16px; }
    .App-header .App-logo-container .App-logo {
      -webkit-animation: App-logo-spin infinite 20s linear;
              animation: App-logo-spin infinite 20s linear;
      height: 80px;
      pointer-events: none; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.App-main {
  padding-top: 15px; }
  .App-main .SearchField {
    margin: 0 8px 15px 8px; }
    .App-main .SearchField input,
    .App-main .SearchField button {
      padding: 6px 14px;
      background: transparent;
      outline: 0; }
    .App-main .SearchField input {
      border: 1px solid #ccc;
      border-right: 0; }
      .App-main .SearchField input:focus {
        border: 1px solid #797979;
        border-right: 0; }
    .App-main .SearchField button {
      border: 1px solid #797979; }
      .App-main .SearchField button:hover {
        background: #ececec; }
  .App-main .grid-item {
    width: calc(25%);
    padding-bottom: 15px; }
    @media (max-width: 767.98px) {
      .App-main .grid-item {
        width: 50%; } }
    @media (max-width: 575.98px) {
      .App-main .grid-item {
        width: 100%; } }
    .App-main .grid-item img {
      width: 100%;
      padding: 0 8px; }

